<template>
    <div class="left">
        <img :src="leftimg" alt="">
    </div>
</template>

<script>
export default {
  name: 'optionImg',
  data(){
      return{
          leftimg:require('@/assets/test.jpg')
      }
  }
}

</script>


<style lang="scss" scoped>
$height-content : 300px;
.left{
    width: 400px;
    height: $height-content;
   img{
      height: 100%;
      width: 100%;
      border-radius: 5px;
      clip-path: polygon(70% 0%, 0% 0%, 0% 215%,100% 50%);
    }
  }
</style>


