import Vue from 'vue'
import Router from 'vue-router'
import register from '@/components/register.vue'
import login from '@/components//signIn/login.vue'
import retrieve from '@/components/signIn/retrievePassword.vue'
import changePassword from '@/components/signIn/changePassword.vue'
import home from '@/components/homepage/home.vue' 

Vue.use(Router)


const router = new Router({
  routes: [
    {
      path: '/',
      component: register,
      children: [ 
    　　　{ 
    　　　　path: '/', 
      　　　component: login 
    　　  }
    　　]
    },
    {
      path: '/register',
      component: register,
      children: [
        {
          path: '/',
          name: 'login',
          component: login
        },
        {
          path: 'retrievePassword',
          name: 'retrieve',
          component: retrieve
        },
        {
          path: 'changePassword',
          name: 'changePassword',
          component: changePassword
        }  
      ]
    },
    {
      path: '/home',
      name: 'home',
      component: home,
      meta: { requiresAuth: true }
    }
  ]
})


router.beforeEach((to, from, next) => {
  console.log(to)
  if (to.meta.requiresAuth) {
   if(!localStorage.getItem('_pc_userName')) {
    const authUser = JSON.parse(window.localStorage.getItem('_pc_token'));
    if (authUser && authUser._pc_userName != '') {
      console.log("pd "+ authUser._pc_userName != '');
      next()
    } else {
      console.log("请先登录老铁~");
      next({name: 'login',params:{"msg":"请先登录老铁~"}})
    }
   } 
}else{
  next()
}
  
})


export default router


