<template>
  <div class="login clear">
      <div class="left">
        <option-img></option-img>
      </div>
      <router-view />
  </div>
</template>

<script>
import optionImg from '../components/signIn/optionImg.vue'
import { constants } from 'fs'
export default {
  name: 'register',
  components:{optionImg},
  data () {
    return {
      
    }
  },
  methods:{
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
$height-content : 300px;

.login{
  background: #fff;
  width: 1000px;
  min-height: $height-content;
  border-radius: 5px;
  margin: auto;
  margin-top: 5%;
  .left{
    width: 400px;
    float: left;
    height: $height-content;
  }
  .right{
    width: 600px;
    height: $height-content;
    float: right;
    position: relative;
    p{
     text-align: center;
    }
   }
  .clear {zoom:1;}    /*==for IE6/7 Maxthon2==*/
  .clear::after {
    clear:both;
    content:'.';
    display:block;
    width: 0;
    height: 0;
    visibility:hidden;
  }
}

</style>



