<template>
    <div>
        <button @click="signOut">退出</button>
    </div>
</template>

<script>
export default {
    name: 'home',
    data(){
        return{
            account:''
        }
    },
    created(){
        
    },
    methods:{
        signOut:function(){
        this.$router.push('/');
           this.$axios({
                method: 'post',
                url: '/index/logout',
                headers	:{'Content-Type': 'application/json'}
           }).then(response => {
               console.log(response);
               if(response.data.code == 200){
                   window.localStorage.removeItem('_pc_token');
                   this.$router.push('/');
               }
               
           }).catch(response => {

           })
        }
    }
}
</script>

<style lang="scss" scoped>

</style>


