<template>
  <div class="right clear">
    <div>
      <p><span>密码更换</span></p>
      <div>
        <div class="block" id="step_1 eye-img">
          <label for=""><img :src="cipherimg" alt=""><input :type="pwdType[0]"  @focus="changeTip" v-model="oldCipher" name="cipher" id="oldCipher" placeholder="旧密码"><img :src="seen1?openeye:nopeneye" class="eye" alt="" @click="changeType(0,'seen1')"></label>
          <label for=""><img :src="cipherimg" alt=""><input :type="pwdType[1]"  @focus="changeTip" v-model="newCipher" name="cipher" id="newCipher" placeholder="新密码"><img :src="seen2?openeye:nopeneye" class="eye" alt="" @click="changeType(1,'seen2')"></label>
          <label for=""><img :src="cipherimg" alt=""><input :type="pwdType[2]"  @focus="changeTip" v-model="comfNewCipher" name="cipher" id="comfNewCipher" placeholder="确认新密码"><img :src="seen3?openeye:nopeneye" class="eye" alt="" @click="changeType(2,'seen3')"></label>
          <p class="annotation">注：密码需要<span>大写、小写、数字</span>的组合，至少8位，最多16位</p>
          <button class="login-btn" @click="changeCipher('step_2')">确认</button>
        </div>
        <div class="none" id="step_2">
            <div class="compelte-margin">
              <img src="@/assets/complete.png" alt="" class="compelte-img">
              <span>恭喜，密码已重置成功</span>
            </div>
            <button class=" login-btn login-btn-2" @click="goHome">直接登录</button>
            <p class="auto-go-tip"><span>{{sendCodeContent}}S</span> 后自动跳转登录页面</p>
        </div>
      </div>
    </div>
    <div class="tips" v-if="errorSeen">
        <el-alert
        :title="errorContent"
        type="error"
        show-icon
        :closable = false>
      </el-alert>
      </div>
  </div>
</template>

<script>

import { error } from 'util';

export default {
  name: 'changePassword',
  data () {
    return {
        account:'',
        seen1:'',
        seen2:'',
        seen3:'',
        newCipher:'',
        comfNewCipher:'',
        oldCipher:'',
        timerC:null,
        ifJump:false,
        sendCodeContent:'',
        pwdType:['password','password','password'] , // 密码类型
        errorSeen: false,
        errorContent:'',
        openeye: require('@/assets/eye.png'), //图片地址
        nopeneye: require('@/assets/noeye.png'), //图片地址
        cipherimg: require('@/assets/cipher.png'),  //图片地址
        leftimg:require('@/assets/test.jpg')
    }
  },
  created(){
    this.account = this.$route.params.accountOA;
  },
  methods:{
      changeCipher:function(event){
        const TIME_COUNT = 5;
        var _that = this;
        console.log(event);
        //串行请求
        new Promise(function (resolve, reject) {
          _that.$axios({
           method: 'post',
            url: '/index/login',
            headers	:{'Content-Type': 'application/json'},
            data: {
              userName : 'yang',
              password : _that.oldCipher
            }
        }).then((response) => {
               resolve(response);
              })
        }).then(function (responseR){
          if(responseR.data.code == 200 || responseR.data.code == 201){
           //密码重置
            if(_that.newCipher != '' && _that.comfNewCipher !='' && _that.newCipher == _that.comfNewCipher){
              if(_that.checkPassword(_that.newCipher )){
                  _that.$axios({
                      method: 'post',
                      url: '/index/resetPassword',
                      headers	:{'Content-Type': 'application/json'},
                      data: {
                        userName :  'admin',
                        newPassword : _that.newCipher
                      }
                    }).then( response => {
                        if(response.data.code != 200){
                          _that.errorContent = response.data.msg;
                          _that.errorSeen = true;
                        }else if(response.data.code == 200){
                          _that.errorSeen = false;
                          _that.changeSiblings(event);

                          const GO_LOING_TIME = 5;

                          var jump = setTimeout(function(){
                              if(!_that.ifJump){
                                _that.$router.push({path:'/'});
                              }else{
                                clearInterval(jump);
                              }
                            },5000)

                          if (!_that.timerC) {
                            _that.sendCodeContent = TIME_COUNT;
                            _that.timerC = setInterval(() => {
                              if ( _that.sendCodeContent > 0 && _that.sendCodeContent <= TIME_COUNT) {
                                _that.sendCodeContent--;
                              } else {
                                 clearInterval(_that.timerC);
                                _that.timerC = null;
                              }
                            }, 1000)
                          }
                        }
                    })
                  }else{
                    _that.errorContent = '密码不符合规则，请重新输入';
                    _that.errorSeen = true;
                }
              }else{
                _that.errorContent = '密码与确认密码不一致，请确保一致';
                _that.errorSeen = true;
              }
             }else{
                _that.errorContent = response.data.msg;
                _that.errorSeen = true;
              }
        }).catch( error => {
            _that.errorContent = '系统错误，请联系运营服务部邓丽娟！';
            _that.errorSeen = true;
        })
      },
      //登录主页
      goHome:function(){
        this.ifJump = true;
        window.localStorage.setItem('_pc_token', JSON.stringify({_pc_userName:this.account }));
        this.$router.push({name:'home',params:{"accountOA":this.account}});
      },
      //监听input是否聚焦
      changeTip: function(){
        this.errorSeen = false ;
        /* if (document.getElementById('oldCipher') == document.activeElement ||
            document.getElementById('newCipher') == document.activeElement ||
            document.getElementById('comfNewCipher') == document.activeElement) {
              this.errorSeen = false ;
              console.log('获取焦点');
          }  */
      },
      //可见密码与不可见密码状态的切换
      changeType(arg0,arg1) {
        this.pwdType[arg0] = this.pwdType[arg0] === 'password' ? 'text' : 'password';
        if(arg1=="seen1"){
            this.seen1 = !this.seen1;//小眼睛的变化
        }
        if(arg1=="seen2"){
            this.seen2 = !this.seen2;//小眼睛的变化
        }
        if(arg1=="seen3"){
            this.seen3 = !this.seen3;//小眼睛的变化
        }
        console.log(arg0,arg1);
      },
      //封装原生js
      siblings:function(elm) {
      var a = [];
      var p = elm.parentNode.children;
      for(var i =0,pl= p.length;i<pl;i++) {
        if(p[i] !== elm) a.push(p[i]);
      }
      return a;
      },
      changeSiblings:function(event){
        console.log("event:"+event);
        document.getElementById(event).setAttribute("class","block");
        var sibl = this.siblings(document.getElementById(event));
        console.log("document.getElementById(event)"+ document.getElementById(event));
        for(let i = 0; i < sibl.length; i++){
          console.log("sibl[i]"+ sibl[i]);
          document.getElementById(sibl[i].getAttribute('id')).setAttribute("class","none");
        }
      },
       //检查密码是否符合规则
      checkPassword : function(cipherP){
        let checkReg =/^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?![0-9a-z]+$)(?![0-9A-Z]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/;
        checkReg.lastIndex = 0;
        console.log(checkReg.test(cipherP),cipherP);
        return checkReg.test(cipherP)?true:false
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
$height-content : 300px;
  .right{
    width: 600px;
    height: $height-content;
    float: right;
    position: relative;
    p{
     text-align: center;
    }
    label{
      display: block;
      border: 1px solid #7f7f7f;
      border-radius: 5px;
      width: 300px;
      height: 40px;
      margin: auto;
      margin-top: 5px;
      position: relative;
      overflow: hidden;
        img:first-child{
        width: 25px;
        padding: 0 10px;
        position: relative;
        left: 0;
        top: 50%;
        transform: translate(0,-50%);
        }
        img:last-child{
          position: absolute;
          right: 0;
          top: 50%;
          transform: translate(0,-50%);
          width: 25px;
          padding: 0 10px;
        }
      
      input{
        font-size: 14px;
        height: 25px;
        width: 230px;
        border: none;
        outline: none;
        padding-left: 5px;
        box-sizing: border-box;
      }
     .yzm{
        display: inline-block;
        width: 105px;
        vertical-align: middle;
        line-height: 40px;
        margin-top: -1px;
        border-left: 1px solid #7f7f7f;
        background: #ffbe5a;
        border-radius: 0;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        padding: 0 5px;
        box-sizing: border-box;
        font-size: 14px;
        color: #fff;
      }
    }
    .compelte-margin{
        margin-top: 60px;
        text-align: center;
        img.compelte-img{
        width: 25px;
        vertical-align: middle;
        }
      }
    .auto-go-tip{
        font-size: 12px;
        margin-bottom: 0;
    }
    button{
      margin: auto;
      display: block;
      width: 80px;
      height: 40px;
      cursor: pointer;
      border-radius: 5px;
      border: none;
      font-size: 16px;
    }
    button.login-btn{
      background: #FFBE5A;
      margin-top: 30px;
      color: white;
    }
    button.login-btn-2{
      width: 120px;
      margin-top: 100px;
    }
    .annotation{
      font-size: 12px;
      span{
        color: red;
      }
    }
    .tips{
      position: absolute;
      min-width: 300px;
      border-radius: 5px;
      left: 50%;
      bottom: 55px;
      transform: translate(-50%, -50%);
      p{
        color: white;
        font-size: 13px;
      }
    }
  }
  .block{
    display: block !important;
  }
  .none{
    display: none !important;
  }

</style>



