<template>
  <div id="app">
    <div class="top-fixed-head">
      <img src="./assets/logo.png" alt="">
      <span>匠心助手</span>
      <span v-if="isShow">欢迎登录</span>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      isShow:true
    }
  },
  created(){
    this.isShowFn();
  },
  methods:{
    isShowFn:function(){
      if(!!localStorage.getItem('_pc_token') && localStorage.getItem('_pc_token') != ''){
        this.isShow = false;
      }
    }
    
  }
}
</script>

<style lang='scss'>
body{
  padding: 0;
  margin: 0;
  background-color: #F7F7F7;
  width: 1200px;
  margin: auto;  
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  width: 100%;
  .top-fixed-head{
    height: 100px;
    line-height: 100px;
    img{
      height: 55px;
      width: auto;
    }
    span:nth-child(2){
      color: #FFA331;
      font-size: 38px;
      font-weight: 700;
      vertical-align: super;
    }
     span:nth-child(3){
      color: #666666;
      font-size: 20px;
      vertical-align: top;
      margin-left: 10px;
    }
  }
  input{
   background-color: rgba(255,255,255,0);
  }
  input:-webkit-autofill{
      transition: background-color 5000s ease-in-out 0s;
      -webkit-text-fill-color: #242424;
  }
  input[name="cipher"]::-ms-clear, input::-ms-reveal{
    display: none;
  }
  button{
    outline: none;
    &:hover{
      background: #ffa331;
    }
  }
}


</style>



