<template>
  <div class="right clear">
    <div>
      <p class="title"><span>密码找回</span></p>
      <div class="step-head">
        <span class="step confirm-account select"><i>1</i>确认账号</span><span class="step security-verification"><i>2</i>安全验证</span><span class="step security-verification new-verification"><i>3</i>重置密码</span><span class="step reset-password"><i>4</i>完成</span>
      </div>
      <div class="step-box">
        <div class="block" id="step1">
          <label for=""><img :src="accountimg" alt=""><input type="text" v-model="accountOA" name="account" id="account" placeholder="OA账号" @focus="changeTip"></label>
          <button class="oa-btn" @click="comfirmOA('step2,oaAccount')">下一步</button>
        </div>
        <div class="none" id="step2">
          <label for=""><img :src="verificationCodeimg" alt=""><input v-model="code" type="tel" autocomplete="off" name="verificationCode" id="verificationCode" placeholder="手机验证码"  @focus="changeTip"><button :disabled="getyzm" :class="[ getyzm ? 'yzm-time' : 'yzm']" id="yzm" @click="sendCode"><span v-show="show" >获取验证码</span><span v-show="!show" >{{sendCodeContent}} s</span></button></label>
          <button class="yzm-btn" @click="comfirmOA('step3,verificationCode')">下一步</button>
        </div>
        <div class="none" id="step3">
          <label for=""><img :src="cipherimg" alt=""><input :type="pwdType[1]" name="cipher" id="newCipher" placeholder="新密码" v-model="newCipher" @focus="changeTip"><img :src="seen2?openeye:nopeneye" class="eye" alt="" @click="changeType(1,'seen2')"></label>
          <label for=""><img :src="cipherimg" alt=""><input :type="pwdType[2]" name="cipher" id="comfNewCipher" placeholder="确认新密码" v-model="comfNewCipher" @focus="changeTip"><img :src="seen3?openeye:nopeneye" class="eye" alt="" @click="changeType(2,'seen3')"></label>
          <p class="annotation">注：密码需要<span>大写、小写、数字</span>的组合，至少8位，最多16位</p>
          <button class="comfirm-btn" @click="comfirmOA('step4,cipher')">确认</button>
        </div>
        <div class="none" id="step4">
          <div class="compelte-margin">
            <img src="@/assets/complete.png" alt="" class="compelte-img">
            <span>恭喜，密码已重置成功</span>
          </div>
          <button class="login-btn" @click="goHome">直接登录</button>
          <p class="auto-go-tip"><span>{{goLoginTime}}S</span> 后自动跳转登录页面</p>
        </div>
      </div>
    </div>
    <div class="tips">
        <el-alert v-if="errorSeen"
        :title="errorContent"
        type="error"
        show-icon
        :closable=false>
      </el-alert>
      <el-alert v-if="iphoneSeen"
        :title="phoneTip"
        type="success"
        show-icon>
      </el-alert>
      </div>
  </div>
</template>

<script>
/* import optionImg from '@/components/signIn/optionImg/optionImg.vue' */
import { error } from 'util'

export default {
  name: 'retrievePassword',
  /* components:{optionImg}, */
  data () {
    return {
        show:true,
        timer: null,
        phone:'',//手机号
        code:'',//存储验证码,
        newCipher:'',//新密码
        comfNewCipher:'',//确认新密码
        errorSeen: false,//错误信息是否可见
        errorContent:'',//错误信息提醒参数
        accountOA:'',//OA账号
        seen2:'',//密码眼睛2
        seen3:'',//密码眼睛2
        iphoneSeen:false,//是否可以看到发送到那个手机
        goLoginTime:'',
        login_timer:null,
        phoneTip:'',
        ifJump:false,
        getyzm:false,//获取验证码的按钮是否可以点击
        pwdType:['password','password','password'] , // 密码类型
        sendCodeContent:'',//验证码按钮文字
        accountimg:require('@/assets/account.png'), //账号图片地址
        phoneimg:require('@/assets/phone.png'), //手机图片地址
        verificationCodeimg:require('@/assets/verificationCode.png'),//验证码图片地址
        openeye: require('@/assets/eye.png'), //密码眼睛图片地址
        nopeneye: require('@/assets/noeye.png'), //密码眼睛图片地址
        cipherimg: require('@/assets/cipher.png'),  //密码图片地址
    }
  },
  methods:{
    //确认是否有这个账号
    //next--下一步
    //current--当前步骤
    comfirmOA:function(event){
      const GO_LOING_TIME = 5;
      const TIME_COUNT= 60;
      let argA = event.split(',')
      let _that = this
      if(argA[1] == 'oaAccount'){
        if(this.accountOA != ''){//用户名的验证

        
        //串行请求
        new Promise(function (resolve, reject) {
          _that.$axios({
           method: 'post',
            url: '/index/forgetPassword',
            headers	:{'Content-Type': 'application/json'},
            data: {
              userName: _that.accountOA
            }
        }).then((response) => {
              resolve(response);
              console.log(response);
          })
        }).then(function (responseR){
         
          _that.changeSiblings(argA[0]);
         
          if(responseR.data.code != 200){
              _that.errorContent = responseR.data.msg;
              _that.errorSeen = true;
              console.log(responseR.data.code);
            }else if(responseR.data.code == 200){
              _that.errorSeen = true;
              document.getElementsByClassName('confirm-account')[0].classList.remove('select');
              document.getElementsByClassName('security-verification')[0].classList.add('select');
              _that.changeSiblings(argA[0]);
              _that.errorSeen = false;
              _that.phone = responseR.data.data;

             _that.$axios({
                  method: 'post',
                  url: '/index/sendCode',
                  headers	:{'Content-Type': 'application/json'},
                  data: {
                    phoneNumber: _that.phone
                  }
                }).then(response => {
                  console.log(response);
                    if(response.data.code != 200){
                      _that.errorContent = response.data.msg;
                      _that.errorSeen = true;
                      _that.iphoneSeen = false;
                    }else if(response.data.code == 200){
                        _that.errorSeen = false;
                        _that.phone = responseR.data.data;
                        _that.phoneTip = '已发送验证码至手机号 '+ responseR.data.data;
                        _that.iphoneSeen = true;
                        setTimeout(function(){
                          _that.iphoneSeen = false;
                        },3000)
                    
                      if (!_that.timer) {
                        _that.sendCodeContent = TIME_COUNT;
                        _that.show = false;
                        _that.getyzm = true;
                      

                        _that.timer = setInterval(() => {
                          if ( _that.sendCodeContent > 0 && _that.sendCodeContent <= TIME_COUNT) {
                            _that.sendCodeContent--;
                          } else {
                            _that.show = true;
                            clearInterval(_that.timer);
                            _that.timer = null;
                            _that.getyzm = false;
                            
                          }
                        }, 1000)
                      }

                    }
                }) 
            } 
          
        })

        }
      }else if(argA[1] == 'verificationCode'){//验证码的验证
          if(this.code != ''){
          this.$axios({
            method: 'post',
            url: '/index/checkMsgCode',
            headers	:{'Content-Type': 'application/json'},
            data: {
              phoneNumber :_that.phone,
              code : _that.code
            }
          }).then(response => {
             console.log(response);
              if(response.data.code != 200){
                _that.errorContent = response.data.msg;
                _that.errorSeen = true;
                console.log(response.data.code);
               
              }else if(response.data.code == 200){
                _that.errorSeen = false;
                document.getElementsByClassName('security-verification')[0].classList.remove('select');
                document.getElementsByClassName('new-verification')[0].classList.add('select');
                _that.changeSiblings(argA[0]);
              }
          }).catch(error => {
             _that.errorContent = '系统错误，请联系运营服务部邓丽娟！';
             _that.errorSeen = true;
          })
        }
      }else if(argA[1] == 'cipher'){//密码重置
          if(this.newCipher != '' && this.comfNewCipher !='' && this.newCipher == this.comfNewCipher){
            if(this.checkPassword(this.newCipher)){
              console.log(this.code);
              this.$axios({
                method: 'post',
                url: '/index/resetPassword',
                headers	:{'Content-Type': 'application/json'},
                data: {
                  userName : this.accountOA,
                  newPassword : this.newCipher
                }
              }).then(response => {
                console.log(response);
                  if(response.data.code != 200){
                    _that.errorContent = response.data.msg;
                    _that.errorSeen = true;
                  
                  }else if(response.data.code == 200){
                    _that.errorSeen = true;
                    document.getElementsByClassName('new-verification')[0].classList.remove('select');
                    document.getElementsByClassName('reset-password')[0].classList.add('select');
                    this.changeSiblings(argA[0]);
                    _that.errorSeen = false;
                    this.phone = response.data.data;

                    var jump = setTimeout(function(){
                      if(!_that.ifJump){
                        _that.$router.push({path:'/'});
                      }else{
                        clearInterval(jump);
                      }
                    },5000)

                    if (! _that.login_timer) {
                       _that.goLoginTime = GO_LOING_TIME;
                       _that.login_timer = setInterval(() => {
                        if (  _that.goLoginTime > 0 &&  _that.goLoginTime <= GO_LOING_TIME) {
                           _that.goLoginTime--;
                        } else {
                          clearInterval( _that.login_timer);
                           _that.login_timer = null;
                        }
                      }, 1000)
                    }

                    console.log(response);
                  }
              }).catch(error => {
                _that.errorContent = '系统错误，请联系运营服务部邓丽娟！';
                _that.errorSeen = true;
              })
            }else{
              _that.errorContent = '密码不符合规则，请重新输入';
              _that.errorSeen = true;
            }
        }else{
              _that.errorContent = '密码与确认密码不一致，请确保一致';
              _that.errorSeen = true;
            }
      }
        
     
    },
    //登录主页
      goHome:function(){
         this.ifJump = true;
         window.localStorage.setItem('_pc_token', JSON.stringify({_pc_userName:this.accountOA }));
         this.$router.push({name:'home',params:{"accountOA":this.accountOA}});
      },
    //点击发送验证码
    sendCode : function(){
      
      const TIME_COUNT= 60;
      var _that = this;
      console.log(event);
      this.$axios({
        method: 'post',
        url: '/index/sendCode',
        headers	:{'Content-Type': 'application/json'},
        data: {
          phoneNumber: _that.phone
        }
      }).then(response => {
         console.log(response);
          if(response.data.code != 200){
            _that.errorContent = response.data.msg;
            _that.errorSeen = true;
            _that.iphoneSeen = false;
          }else if(response.data.code == 200){
            _that.errorSeen = false;
            _that.iphoneSeen = true;
           
            if (!this.timer) {
              this.sendCodeContent = TIME_COUNT;
              this.show = false;
              _that.getyzm = true;
              

              this.timer = setInterval(() => {
                if (this.sendCodeContent > 0 && this.sendCodeContent <= TIME_COUNT) {
                  this.sendCodeContent--;
                } else {
                  this.show = true;
                  clearInterval(this.timer);
                  this.timer = null;
                   _that.getyzm = false;
                  
                }
              }, 1000)
            }

          }
      }).catch(error => {
           _that.errorContent = '系统错误，请联系运营服务部邓丽娟！';
           _that.errorSeen = true;
      })

    },
    //确认按钮
    submitPassword : function(){
      if(this.code != '' && this.iphoneCode == this.code){
        
        
      }else if(this.code == ''){
        // this.$alert('请填写验证码').catch(()=>{})
        this.errorContent = '验证码不能为空，请填写验证码'
        this.errorSeen = 'true';
        

      }else if(this.iphoneCode != this.code){
        // this.$alert('请填写正确的验证码').catch(()=>{})
        this.errorContent = '验证码输入有误，请填写正确的验证码'
        this.errorSeen = 'true';
        
      }
      
    },
    //监听input是否聚焦
    changeTip : function(){
      this.errorSeen = false ;
     /*  if (document.getElementById('account') == document.activeElement ||
          document.getElementById('verificationCode') == document.activeElement ||
          document.getElementById('newCipher') == document.activeElement ||
           document.getElementById('comfNewCipher') == document.activeElement) {
            this.errorSeen = false ;
            console.log('获取焦点');
        }  */
    },
    //今天眼睛的变化
    changeType(arg0,arg1) {
        this.pwdType[arg0] = this.pwdType[arg0] === 'password' ? 'text' : 'password';
        if(arg1=="seen1"){
            this.seen1 = !this.seen1;//小眼睛的变化
        }
        if(arg1=="seen2"){
            this.seen2 = !this.seen2;//小眼睛的变化
        }
        if(arg1=="seen3"){
            this.seen3 = !this.seen3;//小眼睛的变化
        }
        console.log(arg0,arg1);
    },
    //简化js选择兄弟节点
    siblings : function(elm) {
      var a = [];
      var p = elm.parentNode.children;
      for(var i =0,pl= p.length;i<pl;i++) {
        if(p[i] !== elm) a.push(p[i]);
      }
      return a;
    },
    changeSiblings : function(event){
      document.getElementById(event).setAttribute("class","block");
      var sibl = this.siblings(document.getElementById(event));
      
      for(let i = 0; i < sibl.length; i++){
      document.getElementById(sibl[i].getAttribute('id')).setAttribute("class","none");
      }
    },
    //检查密码是否符合规则
    checkPassword : function(cipherP){
      let checkReg =/^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?![0-9a-z]+$)(?![0-9A-Z]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/;
      checkReg.lastIndex = 0;
      console.log(checkReg.test(cipherP),cipherP);
      return checkReg.test(cipherP)?true:false
    }
 }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
$height-content : 300px;
  .right{
    width: 600px;
    height: $height-content;
    float: right;
    position: relative;
    .title{
      text-align: center;
    }
    .step-head{
      text-align: center;
      .step{
        display: inline-block;
        padding: 10px 10px;
        width: 100px;
        background: #eee;
        color: #7e7e7e;
        font-size: 12px;
        i{
          font-style: normal;
          display: inline-block;
          border: 1px solid #7e7e7e;
          width: 15px;
          line-height: 15px;
          border-radius: 100px;
          margin-right: 3px;
        }
      }
      .confirm-account{
        clip-path: polygon(88% 0%, 0% 0%, 0% 500%, 100% 50%);
      }
      .security-verification{
        margin-left: -10px;
        clip-path: polygon(120px 19px, 104px 0px, 0px 0px, 40px 50px, 30px 32px, 83px 37px, 30px 0px, 0px 39px, 57px 100px);
      }
      .reset-password{
        margin-left: -10px;
        clip-path: polygon(150px 0px, 107px 0px, 0px 0px, 40px 50px, 30px 32px, 83px 37px, 32px 0px, 0px 39px, 100px 100px)
      }
      .select{
        color: white;
        background: #FFBE5A;
        i{
          border: 1px solid white;
          color: white;
        }
      }
    }
    .step-box{
      margin-top: 30px;
      text-align:center;
      .auto-go-tip{
        font-size: 12px;
        padding: 0;
        margin-top: 5px;
        color: #7e7e7e;
      }
      p{
      text-align: center;
      }
      .compelte-img{
        width: 25px;
        vertical-align: middle;
      }
      .compelte-margin{
        margin-top: 60px;
      }
      label{
        display: block;
        border: 1px solid #e9e9e9;
        border-radius: 5px;
        width: 300px;
        height: 40px;
        margin: auto;
        margin-top: 5px;
        overflow: hidden;
        img{
          width: 25px;
          padding: 0 10px;
          position: relative;
          left: 0;
          top: 50%;
          transform: translate(0,-50%);
        }
        input{
          font-size: 14px;
          height: 25px;
          width: 150px;
          border: none;
          outline: none;
          padding-left: 5px;
          box-sizing: border-box;
        }
        input[name="account"]{
          width: 250px;
        }
        input[name="cipher"]{
          width: 200px;
          height: 40px;
        }
      
        .yzm{
          display: inline-block;
          width: 105px;
          vertical-align: middle;
          line-height: 40px;
          margin-top: -1px;
          border-left: 1px solid #e9e9e9;
          background: #ffbe5a;
          border-radius: 0;
          border-top-right-radius: 5px;
          border-bottom-right-radius: 5px;
          padding: 0 5px;
          box-sizing: border-box;
          font-size: 14px;
          color: #fff;
        }
        .yzm-time{
          display: inline-block;
          width: 105px;
          vertical-align: middle;
          line-height: 40px;
          margin-top: -1px;
          border-left: 1px solid #e9e9e9;
          background: #999;
          border-radius: 0;
          border-top-right-radius: 5px;
          border-bottom-right-radius: 5px;
          padding: 0 5px;
          box-sizing: border-box;
          font-size: 14px;
          color: #fff;
          cursor: auto;
          &:hover{
            background-color: #999 !important;
          }
        }
    }
    .annotation{
      font-size: 12px;
      span{
        color: red;
      }
    }
    button{
      margin: auto;
      display: inline-block;
      height: 40px;
      width: 80px;
      cursor: pointer;
      border-radius: 5px;
      border: none;
      font-size: 14px;
      background: #FFBE5A;
      color: white;
    }
    button.oa-btn{
      margin-top: 80px;
    }
    button.yzm-btn{
      margin-top: 78px;
    }
    button.login-btn{
      margin-top: 60px;
      width: 120px;
      display: block;
    }
  }
  .tips{
      position: absolute;
      min-width: 300px;
      border-radius: 5px;
      left: 50%;
      bottom: 45px;
      transform: translate(-50%, -50%);
      p{
        color: white;
        font-size: 13px;
      }
    }
  }
  .block{
    display: block !important;
  }
  .none{
    display: none !important;
  }

</style>



